import axios from "axios";

class Home {
  constructor() {
    this.home = axios.create({
      baseURL: process.env.REACT_APP_API_URL,
      withCredentials: true,
    });
  }

  homes = async () => {
    try {
      const res = await this.home.get("/home/main");
      return res.data;
    } catch (error) {
      console.log(error);
    }
  };

  deleteNotification = async (id) => {
    try {
      const res = await this.home.post(`/home/notificationDelete/${id}`);
      return res.data;
    } catch (error) {
      console.log(error);
    }
  };
}

const axiosRequestFunctions = new Home();

export default axiosRequestFunctions;
